//   } 

.pagination ul li.active,
.pagination ul li.numb:hover,
.pagination ul li.numb:focus{
  background: #FF6B08;
  color: white;
}

.pagination ul li.btn:hover,
.pagination ul li.btn:focus{
  background: #F7F7F7;
  color: black;
}